import DefaultLayout from "@/layout/index";
import React from "react";
import styled from "styled-components";

const ListDiv = styled.div`
  display: flex;
  flex-wrap: no-wrap;
  align-items: center;

  padding: 10px;

  width: 100%;
`;
const ListItemDiv = styled.div`
  flex-shrink: 0;

  width: 80px;
  height: 50px;
  margin-right: 10px;
  border-radius: 12px;
  background: #ffffff;
  box-shadow: 10px 10px 27px #d1d1d1, -10px -10px 27px #ffffff;

  cursor: pointer;

  transition: all 0.3s ease;

  :hover {
    box-shadow: 18px 18px 36px #d1d1d1, -18px -18px 36px #ffffff;
  }
`;

const Page = function () {
  return (
    <DefaultLayout>
      <h1>Link List</h1>
      {/* 
      <ListDiv>
        <ListItemDiv></ListItemDiv>
        <ListItemDiv></ListItemDiv>
        <ListItemDiv></ListItemDiv>
        <ListItemDiv></ListItemDiv>
        <ListItemDiv></ListItemDiv>
        <ListItemDiv></ListItemDiv>
      </ListDiv> */}
    </DefaultLayout>
  );
};
Page.meta = {
  layout: import("@/layout/index"),
  developing: true,
};

// eslint-disable-next-line import/no-anonymous-default-export
export default Page;

// 
// id  id, h  id
// nxt[id]  id  id
// val[id]  id 
//  id ,  id
let h = -1,
  nxt = [] as any[],
  val = [] as any[];
let id = 0;

function init() {
  h = -1;
  id = 0;
}

// 
function add(x: any) {
  val[id] = x;
  nxt[id] = h;
  h = id++;
}

function* iter() {
  for (let i = h; ~i; i = nxt[i]) {
    yield val[i];
  }
}
