import React, { useEffect, useRef, useState } from "react";
import { Row, Col, Button, InputNumber } from "antd";
import { Graph /* Cell, Node, Edge, Shape */ } from "@antv/x6";
// import G6 from "@antv/g6";
import DefaultLayout from "@/layout/index";
import { getNodePosition } from "@/components/Heap";

const RED = true;
const BLACK = false;

type RbtNodeProps = {
  key: any;
  val: any;
  left: RbtNode | null;
  right: RbtNode | null;
  color: boolean;
};

class RbtNode {
  key: any;
  val: any;

  left: RbtNode | null = null;
  right: RbtNode | null = null;
  color: boolean;

  constructor(props: RbtNodeProps) {
    this.key = props.key;
    this.val = props.val;
    this.left = props.left;
    this.right = props.right;
    this.color = props.color;
  }
}

// type RbtNode_LR = RbtNode & { left: RbtNode; right: RbtNode };
// type RbtNode_L = RbtNode & { left: RbtNode };
// type RbtNode_R = RbtNode & { right: RbtNode };

/*
Q1 :  2-3  ?
A1 :  ( 3 ),  "",
     , ,  2-3 , .
     
      4 ,  4  "", ,  2-3 , .
*/

export class Rbt {
  //#region root
  private root: RbtNode | null = null; // 
  public getRoot() {
    return this.root;
  }
  //#endregion

  //#region get
  get(key: any): any {
    return this._get(this.root, key);
  }
  private _get(h: RbtNode | null, key: any): any {
    if (h === null) return null;

    if (key === h.key) return h.val;
    else if (key < h.key) return this._get(h.left, key);
    else return this._get(h.right, key);
  }
  //#endregion

  //#region min/max key
  min() {
    if (this.root === null) return null;
    return this._min(this.root);
  }
  private _min(h: RbtNode): any {
    if (h.left === null) return h.key;
    return this._min(h.left);
  }
  max() {
    if (this.root === null) return null;
    return this._max(this.root);
  }
  private _max(h: RbtNode): any {
    if (h.right === null) return h.key;
    return this._max(h.right);
  }
  //#endregion

  //#region put
  put(key: any, val: any) {
    this.root = this._put(this.root, key, val);

    // 
    this.root.color = BLACK;
  }
  //  h  put 
  //  put  h , 
  private _put(h: RbtNode | null, key: any, val: any): RbtNode {
    if (h === null) {
      return new RbtNode({
        // , 23
        key: key,
        val: val,
        left: null,
        right: null,
        color: RED,
      });
    }

    if (key < h.key) h.left = this._put(h.left, key, val);
    else if (key > h.key) h.right = this._put(h.right, key, val);
    else h.val = val;

    if (!this.isRed(h.left) && this.isRed(h.right)) h = this.rotateL(h);
    if (this.isRed(h.left) && h.left && this.isRed(h.left.left))
      h = this.rotateR(h);
    //  putNode ,
    //  2-3-4 
    if (this.isRed(h.left) && this.isRed(h.right)) this.flipColors(h);

    return h;
  }
  //#endregion

  //#region delMin
  delMin() {
    if (this.root === null) return;

    this.root = this._delMin(this.root);
    if (this.root) this.root.color = BLACK;
  }
  private _delMin(h: RbtNode) {
    //  h.left  null , h.right  null 
    //  h.left  null  h.right
    //  return null  h 
    if (h.left === null) return null;

    //  delMin 
    //  h.left  h.left.left 
    if (!this.isRed(h.left) && !this.isRed(h.left.left))
      h = this.moveRedLeft(h);

    // ,  h.left  null,
    //  h.left  null,  moveRedLeft  h.left  null
    h.left = this._delMin(h.left!);

    return this.fix(h);
  }
  //  :  h.left  h.left.left 
  private moveRedLeft(h: RbtNode) {
    //  h  red, h.left & h.right  black
    this.flipColors(h);
    //  h.left !== null, ,  h.right  null
    if (this.isRed(h.right!.left)) {
      h.right = this.rotateR(h.right!);
      h = this.rotateL(h);
      this.flipColors(h);
    }
    return h;
  }
  //#endregion

  //#region delMax
  delMax() {
    if (this.root === null) return;

    this.root = this._delMax(this.root);
    if (this.root !== null) this.root.color = BLACK;
  }
  private _delMax(h: RbtNode) {
    // ,  h  h.right 
    if (this.isRed(h.left)) h = this.rotateR(h);

    // h.right  null ,
    //  h 
    //  h.left  null, 
    //  null 
    if (h.right === null) return null;

    //  delMax 
    //  h.right  h.right.right 
    if (!this.isRed(h.right) && !this.isRed(h.right.left))
      h = this.moveRedRight(h);

    h.right = this._delMax(h.right!);

    return this.fix(h);
  }
  //  :  h.right  h.right.right 
  private moveRedRight(h: RbtNode) {
    this.flipColors(h);
    // h.right  null,  h.left  null
    if (this.isRed(h.left!.left)) {
      h = this.rotateR(h);
      this.flipColors(h);
    }
    return h;
  }
  //#endregion

  //#region del by key
  del(key: any): void {
    if (this.root === null) return;

    this.root = this._del(this.root, key);
    if (this.root !== null) this.root.color = BLACK;
  }
  private _del(h: RbtNode, key: any): RbtNode | null {
    // key  h  key, 
    //  h.left  h.left.left 
    if (key < h.key) {
      //  h.left  null,  key 
      //  h , 
      if (h.left !== null) {
        if (!this.isRed(h.left) && !this.isRed(h.left.left))
          h = this.moveRedLeft(h);

        h.left = this._del(h.left!, key);
      }
    }
    // key >=  h  key
    // (, key = h.key , h.right  null )
    else {
      // : 
      // , key = h.key  key > h.key ,  h  h.right 
      // ,  delMax ,  rotateR
      // , h  h.right 
      if (this.isRed(h.left)) h = this.rotateR(h);

      //  key = h.key  h.right  null
      //  (h  h.right  null,  h.left  null)
      if (key === h.key && h.right === null) return null;

      if (h.right !== null) {
        //  key > h.key  key = h.key  h.right  null
        // , ,  h.right  h.right.right 
        if (!this.isRed(h.right) && !this.isRed(h.right.left))
          h = this.moveRedRight(h);

        //  key = h.key  h.right  null
        //  h  h.right 
        //  h.right 
        if (key === h.key) {
          const minKey = this._min(h.right!);
          h.val = this._get(h.right, minKey); //  val,  key
          h.key = minKey;
          h.right = this._delMin(h.right!);
        } else h.right = this._del(h.right!, key);
      }
    }

    return this.fix(h);
  }
  //#endregion

  //#region util functions
  private isRed(node: RbtNode | null): boolean {
    if (node === null) return false;
    return node.color === RED;
  }
  private rotateL(h: RbtNode) {
    const x = h.right!; //  h , h 

    // 
    h.right = x.left;
    x.left = h;

    // 
    x.color = h.color;
    h.color = RED;

    return x;
  }
  private rotateR(h: RbtNode) {
    const x = h.left!;

    // 
    h.left = x.right;
    x.right = h;

    // 
    x.color = h.color;
    h.color = RED;

    return x;
  }
  //  4 ,  4 ,  h 
  private flipColors(h: RbtNode) {
    h.color = !h.color;
    h.left!.color = !h.left!.color;
    h.right!.color = !h.right!.color;
  }
  private fix(h: RbtNode) {
    if (this.isRed(h.right)) h = this.rotateL(h);
    if (this.isRed(h.left) && this.isRed(h.left!.left)) h = this.rotateR(h);
    if (this.isRed(h.left) && this.isRed(h.right)) this.flipColors(h);
    return h;
  }
  //#endregion
}

const convertRbtToData = (rbt: Rbt) => {
  const data = {
    nodes: [] as any[],
    edges: [] as any[],
  };

  const root = rbt.getRoot();
  if (root === null) return data;

  const getMaxNodeNo = (root: RbtNode, no: number) => {
    let res = no;
    if (root.left !== null) {
      res = Math.max(res, getMaxNodeNo(root.left, no * 2));
    }
    if (root.right !== null) {
      res = Math.max(res, getMaxNodeNo(root.right, no * 2 + 1));
    }
    return res;
  };

  const maxNo = getMaxNodeNo(root, 1);
  const positions = getNodePosition(maxNo, 50, 100, 100);

  const dfs = (root: RbtNode, no: number) => {
    data.nodes.push({
      id: `${root.key}`,
      shape: "circle",
      x: positions[no].left,
      y: positions[no].top,
      width: 50,
      height: 50,
      label: `${root.key}`,
      attrs: {
        body: {
          fill: `${root.color === RED ? "#faa" : "#000"}`,
          stroke: `${root.color === RED ? "#faa" : "#000"}`,
        },
        label: {
          text: `${root.key}`,
          fill: "#fff",
        },
      },
    });

    if (root.left !== null) {
      data.edges.push({
        source: `${root.key}`,
        target: `${root.left.key}`,
        attrs: {
          line: {
            stroke: `${root.left.color === RED ? "#faa" : "#000"}`,
          },
        },
      });

      dfs(root.left, no * 2);
    }
    if (root.right !== null) {
      data.edges.push({
        source: `${root.key}`,
        target: `${root.right.key}`,
      });

      dfs(root.right, no * 2 + 1);
    }
  };

  dfs(root, 1);

  return data;
};

const useRbt = () => {
  const [rbt] = useState(new Rbt());
  const [data, setData] = useState(convertRbtToData(rbt));

  return {
    rbt,
    data,
    setData,
  };
};

const Page = function () {
  const [putKey, setPutKey] = useState(0 as any);
  const [delKey, setDelKey] = useState(0 as any);
  const { rbt, data, setData } = useRbt();
  const graphRef = useRef(null as any);
  const containerRef = useRef(null as any);

  useEffect(() => {
    if (containerRef.current === null) {
      containerRef.current = document.getElementById("rbt-container");
    }
    // const width = container.clientWidth;
    // const height = container.clientHeight;

    if (graphRef.current === null) {
      graphRef.current = new Graph({
        container: containerRef.current,
        background: {
          color: "#fff",
        },
        panning: {
          enabled: true,
        },
        mousewheel: {
          enabled: true,
          modifiers: ["ctrl", "meta"],
        },
      });
    }
    // const graph = new Graph({
    //   container: containerRef.current,
    //   background: {
    //     color: "#fff",
    //   },
    //   panning: {
    //     enabled: true,
    //   },
    //   mousewheel: {
    //     enabled: true,
    //     modifiers: ["ctrl", "meta"],
    //   },
    // });

    graphRef.current.fromJSON(data);
    // graph.fromJSON(data);

    return () => {
      // 
      // graphRef.current!.dispose();
      // graph.dispose();
    };
  });

  return (
    <DefaultLayout>
      <h1>Red Black Tree</h1>
      <Row gutter={10}>
        <Col>
          <InputNumber
            value={putKey}
            onChange={(val) => setPutKey(Number(val))}
          ></InputNumber>
          <Button
            onClick={() => {
              console.log("put");
              rbt.put(putKey, putKey);
              setData(convertRbtToData(rbt));
            }}
          >
            Put
          </Button>
        </Col>
        <Col>
          <InputNumber
            value={delKey}
            onChange={(val) => setDelKey(Number(val))}
          ></InputNumber>
          <Button
            onClick={() => {
              console.log("delete");
              rbt.del(delKey);
              setData(convertRbtToData(rbt));
            }}
          >
            Delete
          </Button>
        </Col>
      </Row>

      {/* <Rbt /> */}
      <div
        id="rbt-container"
        style={{ width: "100%", height: "100%", marginTop: 50 }}
      ></div>
    </DefaultLayout>
  );
};

// eslint-disable-next-line import/no-anonymous-default-export
export default Page;
